@import "../variables";

.categories {
  ul {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    @media (max-width: 565px) {
      gap: 0;
      li:first-of-type {
        width: 100%;
      }
    }

    li {
      background-color: #f9f9f9;
      padding: 13px 30px;
      border-radius: 30px;
      font-weight: bold;
      cursor: pointer;
      transition: background-color 0.1s ease-in-out;
      @include noselect();

      @media (max-width: 565px) {
        width: 50%;
        margin-bottom: 10px;
      }

      &:hover {
        background-color: darken(#f9f9f9, 2%);
      }

      &:active {
        background-color: darken(#f9f9f9, 5%);
      }

      &.active {
        background-color: #282828;
        color: #fff;
      }
    }
  }
}
